$blue: #5915e1;
$black: #333333, #666666, #999999;
$gray: #fafafa, #dbdbdb;
$orange: #ff8e18;
$green: #16c378;
$red: #e85d54;
$hoverBlue: #4711b4;
$hoverOrgange: #cc7113;
$superPurple: #5816e0;
$font333: #333;
$font666: #666;
$font999: #999;
$bgColor: #140736;

.sun-swap-modal.ant-modal {
  .justify-content {
    justify-content: space-between;
  }

  .ant-modal-body {
    border-radius: 5px;
    padding: 0;
    text-align: center;

    .price {
      padding: 25px 0px 30px;

      .param {
        justify-content: space-between;

        &.flex {
          margin-bottom: 12.5px;
        }

        img {
          width: 25px;
          height: 25px;
          margin-right: 2px;

          &:nth-child(2) {
            margin: 0 15px 0 -8px;
          }
        }

        span {
          vertical-align: middle;

          font-size: 16px;

          &.price-key {
            color: #333333;
            text-align: left;
          }
        }

        .price-value {
          text-align: right;

          p,
          span {
            font-size: 16px;
            color: #666666;
          }

          .ml20 {
            margin-left: 20px;
          }
        }
      }

      .v-right {
        font-size: 16px;
        color: #666;
      }
    }

    .errorMsg {
      font-size: 14px;
      color: #ff8e18;
      background: url(../../assets/images/failed.svg) left no-repeat;
      background-size: 13px;
      padding-left: 18px;
      display: inline-block;
      margin: 0 auto 10px;
    }

    .share-title {
      color: #333;
      font-size: 16px;
    }
  }

  .from-token,
  .to-token,
  .add-token,
  .remove-token {
    font-size: 14px;
    color: #999;
    margin-top: 11px;
    margin-left: 4px;

    img {
      width: 20px;
      vertical-align: middle;
      display: none;
    }

    span {
      vertical-align: middle;
      line-height: 20px;
    }
  }
  .ib-parent div,
  .ib-parent p,
  .ib-parent h1,
  .ib-parent h2,
  .ib-parent h3,
  .ib-parent h4,
  .ib-parent h5,
  .ib-parent h6 {
    display: inline-block;
  }

  //max-width: 630px;
  //width: 100%;
  //margin-left: 0 !important;

  .modal-success {
    img {
      width: 100px;
      margin-top: 25px;
    }
  }

  .title {
    margin: 40px 0;
    font-weight: 500;
    color: #333;

    span {
      margin-right: 3px;

      &:last-child {
        margin-right: 0;
      }
    }

    &.big {
      margin: 0 0 5px;
      font-size: 20px;
    }

    &.normal {
      font-size: 15px;
      margin: 0 0 12.5px;
    }

    &.green {
      font-size: 25px;
      color: #16c378;
      margin: 30px 0 15px;
    }
  }

  &.add-gai {
    .amount {
      .amount-num {
        font-size: 30px;
        color: #5915e1;
      }
    }

    .price-value {
      text-align: right;
    }

    .price .param img {
      width: 20px;
      height: 20px;
      margin-right: 10px;
    }

    .amount-tip {
      font-size: 14px;
      color: #999999;
      margin-top: 15px;
    }
  }

  .from-value,
  .to-value,
  .add-value,
  .remove-value {
    font-size: 25px;
    color: #333333;
    letter-spacing: 0;
    text-align: right;
    display: inline-block;
    word-break: break-all;
  }

  .plus {
    font-size: 17.5px;
    color: #5915e1;
    margin: 0 4px;
    top: -2px;

    &.w10 {
      width: 12.5px;
      margin: 0 12.5px;
      margin-top: -7px;
    }
  }

  .add-ver {
    width: 42px;
    height: 20px;
    line-height: 20px;
    background: #f7f7f7;
    border-radius: 9px;
    font-size: 12px;
    color: rgba(26, 26, 26, 0.6);
    margin-left: 8px;
  }

  .from-token,
  .to-token,
  .add-token,
  .remove-token {
    font-size: 14px;
    color: $font999;
    margin-top: 11px;
    margin-left: 4px;

    img {
      width: 20px;
      vertical-align: middle;
      display: none;
    }

    span {
      vertical-align: middle;
      line-height: 20px;
    }
  }

  .supplyLineTitle,
  .middleTip {
    margin-top: 24px;
    font-size: 14px;
    color: #666;
    margin-bottom: -10px;
  }

  .detail-item {
    padding: 10px 0px;
    font-size: 14px;
    color: $font333;

    .green {
      color: $green;
    }

    .orange {
      color: $orange;
    }

    span:last-child {
      max-width: 230px;
      text-align: right;
    }
  }

  .recipient-addr {
    background: #ffffff;
    box-shadow: 0 10px 40px 0 rgba(21, 55, 156, 0.06);
    border-radius: 20px;
    padding: 20px;
    margin-bottom: 20px;

    font-size: 14px;
    color: $font999;

    .address {
      font-size: 16px;
      color: $font333;
      margin-top: 10px;
    }
  }

  .price,
  .detail-slippage {
    padding: 10px 0px;

    font-size: 16px;
    color: $font333;
  }

  .warning {
    font-size: 14px;
    color: #999999;
    padding: 20px 0;
    text-align: left;
  }

  .img {
    height: 50px;
    line-height: 50px;
  }

  .bold {
    font-size: 18px;
    font-weight: 600;
    color: #111;
    margin: 40px 0 20px;
  }

  .red {
    color: #f00;
    // margin-bottom: 30px;
  }

  .lastBtn {
    width: 70%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    margin-top: 30px;
    background: #fff;
    border: 1px solid #666;
    border-radius: 5px;
    color: #555;
    cursor: pointer;
  }

  .amount {
    padding: 15px 0px 10px;
    align-items: center;
    border-bottom: 1px solid rgba(216, 216, 216, 0.5);

    &.pb25 {
      padding-bottom: 25px;
    }

    &.trans-modal {
      border-bottom: none;
    }

    .amount-num {
      font-size: 30px;
      color: #333333;
      min-height: 27px;
      line-height: 27px;
    }

    .amount-name {
      margin-top: 9px;

      font-size: 14px;
      color: #333;

      img {
        width: 20px;
        height: 20px;
        vertical-align: top;
      }

      span {
        margin-left: 10px;
      }
    }

    .symbol {
      margin: 0 30px;
      font-size: 30px;
    }
  }

  .tokens {
    padding-bottom: 0px;
    border-bottom: none;
  }

  .recipient {
    margin-top: 5px;
    display: block !important;
  }

  .endTip {
    opacity: 0.8;
    font-size: 14px;
    color: #999999;
    margin-top: 17.5px;
    display: block;
  }

  &.confirm-swap-modal {
    .price {
      border-top: 1px solid rgba(216, 216, 216, 0.5);
      margin-top: 25px;
    }
  }

  .ant-modal-body {
    .price {
      font-size: 16px;
      padding-bottom: 0px;

      .right {
        font-size: 16px;
        color: #666;
      }
    }

    .detail-list {
      font-size: 16px;
      margin-top: -2.1px;

      span {
        font-size: 16px;
      }

      .detail-item > div > span:last-child {
        line-height: 1;
      }
    }
  }

  .action-btns {
    margin-top: 30px;
  }

  .supplyDescTitle {
    margin-top: 17.5px;
    opacity: 0.8;
    font-size: 14px;
    color: #999999;
  }

