  .ant-modal-body {
    padding: 0 !important;
  }
  .ant-modal-content {
    //padding: 30px 45px;
    border-radius: 20px;
  }
  .ant-modal-close-x {
    // margin: 50px 45px 0 0;
    width: 16px;
    height: 16px;
    line-height: 16px;
    cursor: pointer;

    .anticon {
      svg {
        display: none;
      }

      width: 16px;
      height: 16px;
      background: url(../../assets/images/Close.svg) no-repeat;
    }
  }
  .ant-modal-close {
    top: 30px;
    right: 45px;
  }
  .ant-modal-header {
    border: none;
    border-radius: 50px 50px 0 0;
    padding: 0 0 10px 0;
    color: rgba(0, 0, 0, 0.85);
    background: #fff;
    .ant-modal-title {
      font-size: 20px;
      color: #333333;
      text-align: center;
      font-weight: 500;
      margin: 0;
    }
  }
  .ques {
    color: #999999;
    display: inline-block;
    background: #fff;
    min-width: 18px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    border-radius: 100%;
    margin-right: 10px;
    border: 1px solid #999999;

    font-size: 16px;
    cursor: pointer;
  }
  .mb30 {
    margin-bottom: 30px;
  }
  .between {
    justify-content: space-between;
  }
