@import '../../../assets/css/theme';
.info_box {
  background: $color-info-bg;
  border-radius: 10px;
  border: 1px solid $color-info-border;
  display: flex;
  align-items: center;
  padding: 12px;
  &_item {
    flex: 1;
    text-align: center;
    position: relative;
    &:after {
      width: 1px;
      height: 80%;
      top: 50%;
      transform: translateY(-50%);
      position: absolute;
      right: 0;
      content: '';
      background-color: rgba(255, 255, 255, 0.1);
    }
  }
  &_item:last-child {
    &:after {
      display: none;
    }
  }
  &_label {
    font-size: 12px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
    line-height: 17px;
  }
  &_value {
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
    line-height: 22px;
    margin-top: 10px;
  }
}
